@import "src/sass/mixins";
@import "src/sass/variables";

.main {
    height: 75px;
    display: flex;
    min-height: 20%;

    .container {
        width: $mainWidth;
        margin: 0 auto;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        flex-wrap: wrap;
        min-height: 100%;
        @media screen and (max-width:1520px) {
            justify-content: center;
        }
    }
}
